<template>
  <div class="swaps-outputs">
    <SwapsItemHeader
      v-model:filters="filters"
      v-model:perPage="perPage"
      :radio-buttons="radioButtons"
      :columns="columns"
    />
    <SwapsItemTable
      v-model:page="page"
      :data="data"
      :name="'outputsTable'"
      :columns="activeColumns"
      :per-page="perPage"
      :total-count="totalCount"
    />
  </div>
</template>

<script>
import { computed, reactive } from 'vue'
import SwapsItemTable from '@/admin/views/Swaps/SwapsItem/SwapsItemTable'
import SwapsItemHeader from '@/admin/views/Swaps/SwapsItem/SwapsItemHeader'
import { useGetters, useMutations } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'

export default {
  name: 'SwapsOutputs',
  components: {
    SwapsItemTable,
    SwapsItemHeader
  },
  setup () {
    const {
      getOutputsPage,
      getOutputsFilters,
      getOutputsPerPage,
      getOutputsData: data,
      getOutputsColumns: columns,
      getOutputsTotalCount: totalCount,
      getOutputsActiveColumns: activeColumns
    } = useGetters(MODULES_NAMES.SWAPS)
    const {
      SET_OUTPUTS_PAGE,
      SET_OUTPUTS_FILTERS,
      SET_OUTPUTS_PER_PAGE
    } = useMutations(MODULES_NAMES.SWAPS)

    const filters = computed({
      get: () => getOutputsFilters.value,
      set: SET_OUTPUTS_FILTERS
    })

    const perPage = computed({
      get: () => getOutputsPerPage.value,
      set: SET_OUTPUTS_PER_PAGE
    })
    const page = computed({
      get: () => getOutputsPage.value,
      set: SET_OUTPUTS_PAGE
    })

    const radioButtons = reactive([
      { key: 'all', title: 'All' },
      { key: 'complete', title: 'Complete' },
      { key: 'progress', title: 'In Progress' },
      { key: 'hold', title: 'On Hold' },
      { key: 'expired', title: 'Expired' }
    ])

    return {
      data,
      page,
      perPage,
      filters,
      columns,
      totalCount,
      radioButtons,
      activeColumns
    }
  }
}
</script>

<style lang="scss">
.swaps-outputs {
  flex-grow: 1;
  display: flex;
  flex-direction: column;

  .swaps-item-table {
    flex-grow: 1;
  }
}
</style>
