<template>
  <section class="swaps-item-table">
    <Table
      :data="data"
      :columns="columns"
      fullHeight
      class="swaps-item-table__layout"
    >
      <template #column="{ column }">
        <span class="swaps-item-table__column">{{ t(`swaps.${name}.columns.${column.name}`) }}</span>
      </template>
      <template #cell="{ item, column, row }">
        <CellFormatter
          :column="column"
          :item="item"
          :row="row"
        />
      </template>
    </Table>
    <Pagination
      v-model="pageModel"
      :per-page="perPage"
      :total-count="totalCount"
      class="swaps-item-table__pagination"
    />
  </section>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { computed } from 'vue'
import Table from '@/admin/components/Table/Table'
import Pagination from '@/shared/components/UIKit/Pagination'
import CellFormatter from '@/admin/components/Table/CellFormatter/CellFormatter'

export default {
  name: 'SwapsItemTable',
  components: {
    CellFormatter,
    Pagination,
    Table
  },
  emits: ['update:page'],
  props: {
    data: {
      type: Array
    },
    columns: {
      type: Array
    },
    perPage: {
      type: [String, Number]
    },
    totalCount: {
      type: [String, Number]
    },
    page: {
      type: [String, Number]
    },
    name: {
      type: String
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()

    const pageModel = computed({
      get: () => props.page,
      set: (val) => {
        emit('update:page', val)
      }
    })

    return {
      t,
      pageModel
    }
  }
}
</script>

