<template>
  <section class="swaps-item-header">
    <div class="swaps-item-header__filters">
      <SearchInput
        v-model="filtersModel.search"
        hide-shadow
        :placeholder="t('labels.searchDatabase')"
        class="swaps-item-header__filter swaps-item-header__filter-search"
      />
      <DateRange
        v-model="filtersModel.dateRange"
        :placeholder-from="t('common.from')"
        :placeholder-to="t('common.to')"
        class="swaps-item-header__filter swaps-item-header__filter-date-range"
      />
    </div>
    <div class="swaps-item-header__settings">
      <RadioButtons
        v-model="filtersModel.status"
        :data="radioButtons"
      >
        <template #default="{ item }">
          {{ t('swaps.table.filters.' + item.key) }}
        </template>
      </RadioButtons>
      <div class="d-flex align-items-center">
        <SelectSort v-model="filtersModel.sort"/>
        <SelectShowOnPage v-model="perPageModel"/>
        <SelectColumns :columns="columns" />
      </div>
    </div>
  </section>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { computed } from 'vue'
import DateRange from '@/shared/components/UIKit/Date/DateRange'
import SelectSort from '@/admin/components/Table/Filters/SelectSort'
import SearchInput from '@/admin/components/Inputs/SearchInput'
import RadioButtons from '@/shared/components/UIKit/RadioButtons'
import SelectColumns from '@/admin/components/Table/Filters/SelectColumns'
import SelectShowOnPage from '@/admin/components/Table/Filters/SelectShowOnPage'

export default {
  name: 'SwapsItemHeader',
  components: {
    DateRange,
    SelectSort,
    SearchInput,
    RadioButtons,
    SelectColumns,
    SelectShowOnPage
  },
  emits: ['update:perPage', 'update:filters'],
  props: {
    radioButtons: {
      type: Array,
      default: () => []
    },
    columns: {
      type: Array
    },
    filters: {
      type: Object
    },
    perPage: {
      type: [Number, String]
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()

    const perPageModel = computed({
      get: () => props.perPage,
      set: (val) => {
        emit('update:perPage', val)
      }
    })
    const filtersModel = computed({
      get: () => props.filters,
      set: (val) => {
        emit('update:filters', val)
      }
    })

    return {
      t,
      filtersModel,
      perPageModel
    }
  }
}
</script>

